$primary: #ffe498;
$light: #fff;
$border-style: 4px double #000;

@font-face {
  font-family: 'Gishella';
  src: url('font/GishellaMorely.woff') format('woff'), url('font/GishellaMorely.ttf') format('truetype'); }

body {
  width: 100vw;
  overflow-x: hidden;
  background-color: $primary; }

.header {
  text-align: center;
  font-family: 'Gishella', Helvetica, Tahoma, sans-serif;

  &__top {
    font-size: 32px;
    margin-top: 16px;
    margin-bottom: 16px; }

  &__center {
    display: flex;
    align-items: center;
    justify-content: center; }

  &__title {
    font-size: 64px;

    &--right {
      margin-right: 24px; } }

  &__img {
    margin-right: 32px;
    margin-left: 32px; } }

.image-circle {
  display: inline-block;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  border: 4px double black;
  background-position: center;
  background-size: contain;
  box-shadow: 0px 1px 13px 0px #6e6e6e;

  &--sanka {
    background-image: url('img/sanka.jpg'); }
  &--plant-1 {
    background-image: url('img/plant1.svg'); }
  &--plant-2 {
    background-image: url('img/plant2.svg'); }
  &--plant-3 {
    background-image: url('img/plant3.svg'); } }

.intro-text {
  width: 66%;
  margin: 64px auto;
  padding: 32px;
  border: $border-style;
  font-size: 18px;
  background-color: $light; }

.spice-content {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 40vw;
  overflow-x: hidden;

  &--left {
    .spice-content__image {
      right: -69px;
      transform: rotate(-55deg); }

    .spice-content__text {
      padding-left: 10%;

      &::after {
        border-left: 0px; } } }

  &--right {
    .spice-content__image {
      left: -69px;
      transform: rotate(55deg); }

    .spice-content__text {
      padding-right: 10%;
      align-self: flex-end;

      &::after {
        border-right: 0px; } } }

  &__title {
    text-align: center;
    margin-bottom: 32px; }

  &__text {
    position: relative;
    font-size: 18px;
    width: 60%;
    padding: 32px;
    margin-bottom: 64px;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      border: $border-style;
      background-color: $light;
      z-index: -1; } }

  &__image {
    width: 300px;
    position: absolute; } }

.spice-table {
  display: flex;
  justify-content: space-between;

  &__row {
    margin-right: 16px; }

  &__head {
    font-weight: 600; }

  &__cell {
    margin-bottom: .5rem; } }

.payment-steps {
  list-style: decimal;
  text-align: left;
  margin-bottom: 32px; }

.payment-details {
  display: flex;
  justify-content: space-between;

  p {
    margin-bottom: 0; }

  &__title {
    display: block;
    margin-bottom: 16px; } }

.text-small {
  font-size: 13px; }
